
/*
 * t: current time
 * b: beginning value
 * c: change in value
 * d: duration
*/
const Tween = {
  linear: function (t, b, c, d) {
    return c * t / d + b
  },
  easeIn: function (t, b, c, d) {
    return c * (t /= d) * t * t + b
  },
  easeOut: function (t, b, c, d) {
    return c * ((t = t / d - 1) * t * t + 1) + b
  },
  easeInOut: function (t, b, c, d) {
    if ((t /= d / 2) < 1) return c / 2 * t * t * t + b
    return c / 2 * ((t -= 2) * t * t + 2) + b
  }
}
// 
function Animation (from, to, duration, easing, callback) {
  var isFunction = function (obj) {
    return typeof obj === 'function'
  }
  var isNumber = function (obj) {
    return typeof obj === 'number'
  }
  var isString = function (obj) {
    return typeof obj === 'string'
  }

  // 
  var toMillisecond = function (obj) {
    if (isNumber(obj)) {
      return obj
    } else if (isString(obj)) {
      if (/\d+m?s$/.test(obj)) {
        if (/ms/.test(obj)) {
          return 1 * obj.replace('ms', '')
        }
        return 1000 * obj.replace('s', '')
      } else if (/^\d+$/.test(obj)) {
        return +obj
      }
    }
    return -1
  }

  if (!isNumber(from) || !isNumber(to)) {
    if (window.console) {
      console.error('fromto')
    }
    return 0
  }

  // duration, easing, callback
  // 
  var options = {
    duration: 300,
    easing: 'linear',
    callback: function () {}
  }

  var setOptions = function (obj) {
    if (isFunction(obj)) {
      options.callback = obj
    } else if (toMillisecond(obj) !== -1) {
      options.duration = toMillisecond(obj)
    } else if (isString(obj)) {
      options.easing = obj
    }
  }
  setOptions(duration)
  setOptions(easing)
  setOptions(callback)

  // requestAnimationFrame
  if (!window.requestAnimationFrame) {
    window.requestAnimationFrame = function (fn) {
      return setTimeout(fn, 17)
    }
  }
  if (!window.cancelAnimationFrame) {
    window.cancelAnimationFrame = function (id) {
      clearTimeout(id)
    }
  }

  // 
  var start = 0
  // during
  var during = Math.ceil(options.duration / 17)
  // 
  var req = null

  // 

  var arrKeyTween = options.easing
  var fnGetValue

  fnGetValue = Tween[arrKeyTween]
  if (isFunction(fnGetValue) === false) {
    console.error('"' + options.easing + '"')
    return
  }

  // 
  var step = function () {
    // 
    var value = fnGetValue(start, from, to - from, during)

    // 
    start++
    // 
    if (start <= during) {
      let cbres = options.callback(value)
      if (!cbres) {
        req = requestAnimationFrame(step)
      } else {
        // ...
        options.callback(to, true)
        cancelAnimationFrame(req)
      }
    } else {
      // ...
      options.callback(to, true)
      cancelAnimationFrame(req)
    }
  }
  // 
  step()
  return req
}

export default Animation
